<template>
  <div>
    ボタンA<child-component v-on:call-parent="parentFunc" foo="1" bar="A" />
    ボタンB<child-component v-on:call-parent="parentFunc" foo="2" bar="B" />
  </div>
</template>

<script>
export default {
  methods: {
    parentFunc(foo, bar) {
      console.log(foo)
      console.log(bar)
    }
  }
}
</script>



