import Vue from 'vue';
import Router from 'vue-router';
import TestPage1 from '@/components/Tests/TestPage1/TestPage1.vue';
import TestPage2 from '@/components/Tests/TestPage2/TestPage2.vue';
import TestPage3 from '@/components/Tests/TestPage3/TestPage3.vue';
import TestPage4 from '@/components/Tests/TestPage4/TestPage4.vue';
import TestPage5 from '@/components/Tests/TestPage5/TestPage5.vue';
import TestPage6 from '@/components/Tests/TestPage6/TestPage6.vue';
//import HelloChild from '@/components/HelloChild/HelloChild.vue'
Vue.use(Router);
export default new Router({
    routes: [
        {
            path: '/',
            name: 'NameTestPage1',
            component: TestPage1
        },
        {
            path: '/page2',
            name: 'NameTestPage2',
            component: TestPage2
        },
        {
            path: '/page3',
            name: 'NameTestPage3',
            component: TestPage3
        },
        {
            path: '/page4',
            name: 'NameTestPage4',
            component: TestPage4
        },
        {
            path: '/page5',
            name: 'NameTestPage5',
            component: TestPage5
        },
        {
            path: '/page6',
            name: 'NameTestPage6',
            component: TestPage6
        },
    ]
});



